.diamondOff{
    display: none;
   
}
.diamondActive {
   display: block;
   width: 100px;
   height: 100px;
  position: absolute;
  z-index: 1;
  opacity: 100%;
  animation-name: move; 
  animation-iteration-count: 1; 
  animation-duration: 5s; 
  animation-timing-function:ease-in; 
} @keyframes move {
  0%{transform: translateY(30px);
    opacity:100%;}
  100%{ opacity:20%; } 
 
  
}